<template>
	<div class="wrap">
		<div id="index" class="cabinIndex">
			<div class="content">
			    <div class="overview">
			    	<div class="module_1">
			    		<div class="module-title">
			    			<span>统计截止时间</span>
							<el-date-picker v-model="valueData" type="month" placeholder="选择月" :picker-options="pickerOptions" 
							@change="changeDate" value-format="yyyy-MM">
							</el-date-picker>
			    		</div>
			    		<div class="module-content">
			    			<div class="content_1">
			    				<div>项目总数</div>
			    				<div class="module-num">
			    					<span id="proj-all">{{projAll}}</span>个
			    				</div>
			    			</div>
			    			<div class="content_1">
			    				<div>前期项目</div>
			    				<div class="module-num">
			    					<span id="proj-before">{{projBefore}}</span>个
			    				</div>
			    			</div>
			    			<div class="content_1">
			    				<div>在建项目</div>
			    				<div class="module-num">
			    					<span id="proj-on">{{projOn}}</span>个
			    				</div>
			    			</div>
			    		</div>
			    	</div>
			    	<div class="module_2">
			    		<div class="module-title">
	                        <img class="left_logo" src="../assets/img/investment.png" />
			    			<span>投资</span>
			    		</div>
			    		<div class="module-content">
							<div class="box">
	                            <div class="box-title">
	                            	<span class="monthSelect">{{choseMonth}}</span>月完成
	                            	<i class="yuan" id="invest-month">{{investMonth}}</i>
	                            	<i id="INVEST_MONEYMTYPE">{{investMonthUnit}}</i>
	                            </div>
								<div class="ok">
									<div class="ok-title">月完成率</div>
									<div class="ok-rate">
										<span id="invest-month-rate">{{investMonthRate}}</span>
									</div>
	                            </div>
								<div class="tbox">
									<el-progress :percentage="investMonthPer" :stroke-width="12" :show-text="false"></el-progress>
								</div>
							</div>
							<div class="box">
								<div class="box-title">
									<span class="yearSelect">{{choseYear}}</span>年完成
									<i class="year-yuan" id="invest-year">{{investYear}}</i>
									<i id="INVEST_MONEYYTYPE">{{investYearUnit}}</i>
								</div>
								<div class="ok">
									<div class="ok-title">年完成率</div>
									<div class="ok-rate">
										<span id="invest-year-rate">{{investYearRate}}</span>
									</div>
								</div>
								<div class="tbox">
									<el-progress :percentage="investYearPer" :show-text="false" :stroke-width="12" ></el-progress>
								</div>
							</div>
			    		</div>
			    	</div>
			    	<div class="module_2">
			    		<div class="module-title">
							<img class="left_logo" src="../assets/img/pay.png" />
			    			<span>支付</span>
			    		</div>
			    		<div class="module-content">
							<div class="box">
								<div class="box-title module2_box_title">
									<span class="monthSelect">{{choseMonth}}</span>月完成
									<i class="yuan" id="pay-month">{{payMonthMoney}}</i>
									<i id="PAY_MONTHTYPE">{{payMonthMoneyUnit}}</i>
								</div>
	                            <div class="box-title module2_box_title">
	                            	<span class="yearSelect">{{choseYear}}</span>年完成<i class="yuan" id="pay-year">{{payYearMoney}}</i>
	                            	<i id="PAY_YEARTYPE">{{payYearMoneyUnit}}</i>
	                            </div>
	                            <div class="box-title module2_box_title">汇总完成
	                            	<i class="yuan" id="pay-total">{{payTotalMoney}}</i>
	                            	<i id="PAY_TOTLETYPE">{{payTotalMoneyUnit}}</i>
	                            </div>
								<div class="ok">
									<div class="ok-title">整体支付进度</div>
								</div>
								<div class="tbox payfor">
									<el-progress :percentage="payRate" :stroke-width="12" :show-text="false"></el-progress>
								</div>
							</div>
			    		</div>
			    	</div>
			    	<div class="module_2 module_3">
			    		<div class="module-title">
	                        <img class="left_logo" src="../assets/img/bidding.png" />
			    			<span>招投标</span>
			    		</div>
			    		<div class="module-content">
							<div class="box">
								<div class="box-title">
									<span class="monthSelect">{{choseMonth}}</span>月完成
								</div>
								<div class="ok last-two">
									<div class="ok-title">招标额
	                                     <span class="yuan" id="bidding-month">{{tenderAmoutMonthRate}}</span>
	                                     <i id="BIDDING_MONEYMTYPE">{{BIDDING_MONEYMTYPE}}</i>
	                                </div>
	                                <div class="ok-rate">招标数
	                                     <span class="yuan" id="biddingnum-month">{{tenderAmoutMonthNum}}</span>
	                                     <i>个</i>
	                                </div>
								</div>
							</div>
							<div class="box">
								<div class="box-title">
									<span class="yearSelect">{{choseYear}}</span>年完成
								</div>
								<div class="ok last-two">
									<div class="ok-title">招标额
										<span class="yuan" id="bidding-year">{{tenderAmoutYearRate}}</span>
										<i id="BIDDING_MONEYYTYPE">{{BIDDING_MONEYYTYPE}}</i>
									</div>
									<div class="ok-rate">招标数
										<span class="yuan" id="biddingnum-year">{{tenderAmoutYearNum}}</span>
										<i>个</i>
									</div>
								</div>
							</div>
			    		</div>
			    	</div>
			    	<div class="module_2 module_3">
			    		<div class="module-title">
	                        <img class="left_logo" src="../assets/img/contract.png" />
			    			<span>合同</span>
						</div>
			    		<div class="module-content">
							<div class="box">
								<div class="box-title">
									<span class="monthSelect">{{choseMonth}}</span>月完成
								</div>
								<div class="ok last-two">
									<div class="ok-title">合同额
										<span class="yuan" id="contract-month">{{monthContractRate}}</span>
										<i id="CONTRACT_MONEYMTYPE">{{CONTRACT_MONEYMTYPE}}</i>
									</div>
									<div class="ok-rate">合同数
										<span class="yuan" id="contractnum-month">{{monthContractNum}}</span>
										<i>个</i>
									</div>
								</div>
							</div>
							<div class="box">
								<div class="box-title">
									<span class="yearSelect">{{choseYear}}</span>年完成
								</div>
								<div class="ok last-two">
									<div class="ok-title">合同额
										<span class="yuan" id="contract-year">{{yearContractRate}}</span>
										<i id="CONTRACT_MONEYYTYPE">{{CONTRACT_MONEYYTYPE}}</i>
									</div>
									<div class="ok-rate">合同数
										<span class="yuan" id="contractnum-year">{{yearContractNum}}</span>
										<i>个</i>
									</div>
								</div>
							</div>
				    	</div>
				    </div>
				</div>
				
				<!--地图-->
				<div class="left_b">
					<div class="map">
						<div class="map-container">
						    <div class="search-box">
								<el-input
								  placeholder="项目查找"
								  v-model="mapInput"
								  clearable>
								</el-input>
								<div class="iconFind" @click="mapSeach">
									<img src="../assets/img/minify.png" style="height:22px; margin:9px 15px;"/>
								</div>
							</div>
						    <div id="map-box" class="map-box"></div>
						</div>
					    <div class="enlarge" id="enlarge">
							<img src="../assets/img/enlarge.png" />
	                    </div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import avatar from '../assets/img/szwb.png'

	export default{
		data:function(){
			return{
				valueData:'',
				pickerOptions:{
				  disabledDate(time) {
		            return time.getTime() > Date.now();
		          }
		        },
		        endData:'',
		        projAll:'',
		        projBefore:'',
		        projOn:'',
		        choseMonth:'',          //选择的月份
		        choseYear:'',			//选择的年份
		        monthContractRate:'',  //月合同额
		        monthContractNum:'',  //月合同个数
		        yearContractRate:'',  //年合同额
		        yearContractNum:'',		//年合同数
		        CONTRACT_MONEYMTYPE:'',  //月合同额单位
		        CONTRACT_MONEYYTYPE:'',  //年合同额单位
		        investMonth:'',          //投资月完成
		        investYear:'',          //投资年完成
		        investMonthRate:'',          //投资月完成率
		        investYearRate:'',          //投资年完成率
		        investMonthPer:0,          //投资月完成进度条
		        investYearPer:0,             //投资年完成进度条
		        payMonthMoney:'',              //支付月完成
		        payYearMoney:'',               //支付年完成
		        payTotalMoney:'',               //汇总完成
		        payRate:0,                     //整体支付进度
		        tenderAmoutYearNum:'',         //招标个数年
		        tenderAmoutMonthNum:'',         //招标个数月
		        tenderAmoutMonthRate:'',         //招标额年
		        tenderAmoutYearRate:'',         //招标额年
		        BIDDING_MONEYYTYPE:'',           //招投标单位年
		        BIDDING_MONEYMTYPE:'',           //招投标单位月
		        investYearUnit:'',                //投资年完成单位
		        investMonthUnit:'',                //投资月完成单位
		        payMonthMoneyUnit:'',              //支付月完成单位
		        payYearMoneyUnit:'',              //支付年完成单位
		        payTotalMoneyUnit:'',              //支付总完成单位
		        mapInput:'' ,                       //地图搜索框值
		        avatar:avatar                    //地图锚点图片
			}
		},
		created(){
			this.getNowMonth();
			this.getHeaderSelData();
			//this.initMap();
		},
		watch:{
			endData:function(){
				this.getHeaderSelData();
			}
		},
		methods:{
			//当前日期
			getNowMonth:function(){
				 var date = new Date;
				 var year = date.getFullYear(); 
				 var month = date.getMonth()+1;
				 if(month < 10){
				 	month = '0' + month;
				 }else{
				 	month = month;
				 }
				 this.endData = year + month;  
				 this.valueData = year + '-' + month;  //给element ui设置默认值
				 this.choseMonth = month;
				 this.choseYear = year;
			},
			getHeaderSelData:function(){
				var that = this;
				var data = this.endData;
				var urlContent = '/api/txieasyui?taskFramePN=ProjectInfoCount&command=getPVariousStatistics&colname=json&colname1=%7B%27dataform%27:%27eui_form_data%27%7D&timeStramp='+data+'&orgID=';

				var urlOtherHeader = '/api/txieasyui?taskFramePN=ProjectInfoCount&command=getPayAndInvest&colname=json&colname1=%7B%27dataform%27:%27eui_form_data%27%7D&timeStramp='+data+'&orgID=';

				this.$http.get(urlContent).then(function (response) {
					that.projAll = response.data.data.PRJ_ALL; //项目总数
					that.projBefore = response.data.data.PRJ_BEF; //前期项目
					that.projOn = response.data.data.PRJ_ON; //前期项目
					that.monthContractRate = response.data.data.CONTRACT_MONEYM;   //月合同额
					that.monthContractNum = response.data.data.CONTRACT_TOTLEM;   //月合同数
					that.yearContractRate = response.data.data.CONTRACT_MONEYY;   //年合同额
					that.yearContractNum = response.data.data.CONTRACT_TOTLEY;   //年合同数

					that.tenderAmoutMonthRate = response.data.data.BIDDING_MONEYM;
					that.tenderAmoutMonthNum = response.data.data.BIDDING_TOTLEM;
					that.tenderAmoutYearRate = response.data.data.BIDDING_MONEYY;
					that.tenderAmoutYearNum = response.data.data.BIDDING_TOTLEY;

					that.CONTRACT_MONEYMTYPE = that.unit(response.data.data.CONTRACT_MONEYMTYPE);

					that.CONTRACT_MONEYYTYPE = that.unit(response.data.data.CONTRACT_MONEYYTYPE);

					that.BIDDING_MONEYMTYPE = that.unit(response.data.data.BIDDING_MONEYMTYPE);

					that.BIDDING_MONEYYTYPE = that.unit(response.data.data.BIDDING_MONEYYTYPE);
				}).catch(function (response) {
				    console.log(response);
				});

				this.$http.get(urlOtherHeader).then(function (response) {
					that.investMonth = response.data.data.INVEST_MONEYM;
					that.investMonthRate = response.data.data.INVEST_RATEM;
					that.investYear = response.data.data.INVEST_MONEYY;
					that.investYearRate = response.data.data.INVEST_RATEY;
					that.investMonthPer = parseFloat(response.data.data.INVEST_RATEM.replace("%","")); //投资月完成率

					that.investYearPer = parseFloat(response.data.data.INVEST_RATEY.replace("%","")); //投资年完成率

					that.payMonthMoney = response.data.data.PAY_MONTH;
					that.payYearMoney = response.data.data.PAY_YEAR;
					that.payTotalMoney = response.data.data.PAY_TOTLE;
					that.payRate = parseFloat(response.data.data.PAY_RATE.replace("%",""));

					that.investMonthUnit = that.unit(response.data.data.INVEST_MONEYMTYPE);

					that.investYearUnit = that.unit(response.data.data.INVEST_MONEYYTYPE);

					that.payMonthMoneyUnit = that.unit(response.data.data.PAY_MONTHTYPE);

					that.payYearMoneyUnit = that.unit(response.data.data.PAY_YEARTYPE);

					that.payTotalMoneyUnit = that.unit(response.data.data.PAY_TOTLETYPE);
				}).catch(function (response) {
				    console.log(response);
				});
			},
			//单位封装
			unit:function(obj){
				//console.log(objW);
				var objUnit = "";
				if(obj == 1){  //单位
					objUnit = "亿元";
				}else if(obj == 2){
					objUnit = "万元";
				}else if(obj == 3){
					objUnit = "元";
				}

				return objUnit;
			},
			//时间控件改变时
			changeDate:function(val){
				this.endData = val;
			},
			//锚点表格
			showProjInfo:function(data){
				var that=this;

				var url = "/api//txieasyui?taskFramePN=DriverPorjet&command=getProjectInfoByCode&colname=json_ajax&colname1={%27dataform%27:%27eui_variable_data%27,%27variable%27:%27returnJson%27}&code="+data.EAF_ID;

				this.$http.post(url).then(function (response) {
					if(response.data.code == 200){
							that.sContent = '<div class="divt"></div><div class="infoWin" style="width:348px;height:230px;color:#666;border-radius:12px;margin-top: 14px;">'+
							'<table>'+
								'<tr><th class="info-table-th">项目名称  </th><td class="info-table-th">'+response.data.EAF_NAME+'</td></tr>'+
							    '<tr><th>项目阶段  </th><td>'+(response.data.PJ_STAGE == null || response.data.PJ_STAGE == undefined ? "" : response.data.PJ_STAGE)+'</td></tr>'+
							    '<tr><th>开工时间  </th><td>'+(response.data.PJ_STARTTIME == null || response.data.PJ_STARTTIME == undefined ? "" : response.data.PJ_STARTTIME)+'</td></tr>'+
							    '<tr><th>投资金额（万元） </th><td>'+(response.data.PJ_JH == null || response.data.PJ_JH == undefined ? "" : response.data.PJ_JH)+'</td></tr>'+
							    '<tr><th>支付金额（万元） </th><td>'+(response.data.PJ_ZF == null || response.data.PJ_ZF == undefined ? "" : response.data.PJ_ZF)+'</td></tr>'+
							    '<tr><th>合同金额（万元） </th><td>'+(response.data.PJ_COR == null || response.data.PJ_COR == undefined ? "" : response.data.PJ_COR )+'</td></tr>'+
							'</table>'+
					    '</div>';
					}
				});
				return that.sContent;
			},
			//地图搜索
			mapSeach:function(){
				console.log(this.mapInput);
			}
		},
		mounted() {
			var self = this;
			var isfand=false;
			var map = new BMap.Map("map-box", {mapType: BMAP_HYBRID_MAP,enableMapClick: false, minZoom:12, maxZoom: 100});//禁止点击
		    map.centerAndZoom(new BMap.Point(114.060913,22.587571),13);//定位深圳，级别13级
		    map.enableScrollWheelZoom();   //启用滚轮放大缩小，默认禁用
		    map.enableContinuousZoom(); 	  //启用地图惯性拖拽，默认禁用
	        var b = new BMap.Bounds(new BMap.Point(113.477686,22.310655),new BMap.Point(114.688458,23.016071)); // 范围 左下角，右上角的点位置 
	        try {    // js中尽然还有try catch方法，可以避免bug引起的错误
	          BMapLib.AreaRestriction.setBounds(map, b); // 已map为中心，已b为范围的地图
	        } catch (e) {
	        // 捕获错误异常
	          //console.log(e);
	        }
			
	        var myCtrl1 = new Con1();
	        map.addControl(myCtrl1);
	       
	        var myCtrl2 = new Con2();
	        map.addControl(myCtrl2);

	        //切换地图显示类型
			function Con1() {
			    this.defaultAnchor = BMAP_ANCHOR_TOP_RIGHT;
			    this.defaultOffset = new BMap.Size(90 , 24);

			    var div1 = document.createElement("div");
			    var e1 = document.createTextNode("透视地图");
			    div1.appendChild(e1);
			    // 设置样式    
			    div1.style.cssText = "opacity:0.8;font-weight:bold;width:80px;text-align:center;line-height:38px;height:38px;font-size:14px;color:#fff;cursor:pointer;border:1px solid CornflowerBlue;background:CornflowerBlue;right:90px;position:absolute;top:24px;";
			    // 绑定事件 
			    div1.onclick = function () {
			       //map.setMapType(map.setMapType(BMAP_NORMAL_MAP));
			    	map.setMapType(BMAP_PERSPECTIVE_MAP);     //修改地图类型为3D地图
			        map.setCurrentCity("深圳市");  //设置当前城市
			    }
			    map.getContainer().appendChild(div1);
			    return div1;
			}
			//切换地图显示类型
			function Con2() {
			    this.defaultAnchor = BMAP_ANCHOR_TOP_RIGHT;
			    this.defaultOffset = new BMap.Size(180 , 24);

			    var div2 = document.createElement("div");
			    var e2 = document.createTextNode("混合地图");
			    div2.appendChild(e2);
			    // 设置样式    
			    div2.style.cssText = "opacity:0.8;font-weight:bold;width:80px;text-align:center;line-height:38px;height:38px;font-size:14px;color:#fff;cursor:pointer;border:1px solid CornflowerBlue;background:CornflowerBlue;right:180px;position:absolute;top:24px;";
			    
			    div2.onclick = function () {
			    	map.setMapType(BMAP_HYBRID_MAP);
			    	map.setCurrentCity("深圳市");  //设置当前城市
			    }
			    
			    map.getContainer().appendChild(div2);
			    return div2;
			}

			this.getMarker=function(){
				var that = this;
				//为地图设置锚点
				this.$http.post("/api/txieasyui?taskFramePN=DriverPorjet&command=getGovProjectList&colname=json_ajax&colname1={%27dataform%27:%27eui_datagrid_data%27,%27tablename%27:%27detail0%27}&dept_id=allProject&bim_stage=&bim_pdept=&bim_projtype=&eaf_name=").then(function (response) {

					if (response.data.rows&&response.data.rows.length>0) {
						for(var i=0;i<response.data.rows.length;i++){
							var lat = response.data.rows[i].BIM_LATITUDE,//经度
						    	lot = response.data.rows[i].BIM_LONGITUDE; //纬度
						    var point = new BMap.Point(lot, lat);

							addMarker(point,response.data.rows[i]);
						}
					}
				});
			}
			
			this.getMarker();

			function addMarker(point,data){
				var that = this;
				var redIcon={icon:new BMap.Icon(self.avatar, new BMap.Size(20,20))};
				var marker=new BMap.Marker(point,redIcon);
				marker.addEventListener("click", function(){
					var opt = {
						width:350,
		 				height:240
					}

					//var that=this;
					var sContent='';
					var url = "/api/txieasyui?taskFramePN=DriverPorjet&command=getProjectInfoByCode&colname=json_ajax&colname1={%27dataform%27:%27eui_variable_data%27,%27variable%27:%27returnJson%27}&code="+data.EAF_ID;

					self.$http.get(url).then(function (response) {
						if(response.data.code == 200){
								sContent = '<div class="divt"></div><div class="infoWin" style="width:348px;height:230px;color:#666;border-radius:12px;margin-top: 14px;">'+
								'<table>'+
									'<tr><th class="info-table-th">项目名称  </th><td class="info-table-th">'+response.data.data[0].EAF_NAME+'</td></tr>'+
								    '<tr><th>项目阶段  </th><td>'+(response.data.data[0].PJ_STAGE == null || response.data.data[0].PJ_STAGE == undefined ? "" : response.data.data[0].PJ_STAGE)+'</td></tr>'+
								    '<tr><th>开工时间  </th><td>'+(response.data.data[0].PJ_STARTTIME == null || response.data.data[0].PJ_STARTTIME == undefined ? "" : response.data.data[0].PJ_STARTTIME)+'</td></tr>'+
								    '<tr><th>投资金额（万元） </th><td>'+(response.data.data[0].PJ_JH == null || response.data.data[0].PJ_JH == undefined ? "" : response.data.data[0].PJ_JH)+'</td></tr>'+
								    '<tr><th>支付金额（万元） </th><td>'+(response.data.data[0].PJ_ZF == null || response.data.data[0].PJ_ZF == undefined ? "" : response.data.data[0].PJ_ZF)+'</td></tr>'+
								    '<tr><th>合同金额（万元） </th><td>'+(response.data.data[0].PJ_COR == null || response.data.data[0].PJ_COR == undefined ? "" : response.data.data[0].PJ_COR )+'</td></tr>'+
								'</table>'+
						    '</div>';
						}

						var infoWindow = new BMap.InfoWindow(sContent,opt);
						map.openInfoWindow(infoWindow,point);
					});
				});

				map.addOverlay(marker);
			}
		}
	}
</script>
<style type="text/css" scope>
	@import "../assets/css/cabin.css";
	.module-title .el-date-editor.el-input{
		margin-left: 20px;
		width: 120px;
	}

	.module-title .el-input__inner{
		height: 30px;
		line-height: 30px;
	}

	.search-box .el-input{
		width:auto;
	}

	.search-box .el-input__inner{
		width:300px;
		border-radius: 0;
	}
</style>


